:root {
  --secondary-light: #e0e0e0;
  --primary-akzent: #4f2ee8;
  --secondary-akzent: #eac645;
  --default-primary-color: #fff;
  --default-secondary-color: #111;
  --user-bg: #f6f6f6;
  --support-bg: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  --support-shadow: 0px 10px 10px #f5f5f71a;
  --text-secondary-light: #b4afaf;
  --primary-akzent-rgba: #4f2ee866;
  --time: .3s;
  --cubic: linear;
  --mobile-menu-bg: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  --switcher-bg-light: linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
  --switcher-bg-dark: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
  --switcher-status: linear-gradient(180deg, #fff 0%, #f7f7f7 100%);
  --switcher-shadow: inset 1px 1px 2px #0000001a;
  --switcher-status-shadow: 1px 1px 2px #0000001a, 0px 2px 2px #0000001a, 1px 2px 4px #0000001a;
}

.light {
  --primary-bg: #f6f6f6;
  --secondary-bg: #fff;
  --primary-color: #111;
  --secondary-color: #fff;
  --logo-color: #000;
  --list-category-accent-text: #4f2ee8;
  --list-category-text: #1119;
  --scroll: #e0e0e0;
  --shopping-list-text: #0009;
  --pop-up-info-text: #11111180;
  --form-button-color: #f3f3f3;
}

.dark {
  --primary-bg: #202024;
  --secondary-bg: #111;
  --primary-color: #fff;
  --secondary-color: #111;
  --logo-color: #f3f3f3;
  --list-category-accent-text: #eac645;
  --list-category-text: #fff9;
  --scroll: #1119;
  --shopping-list-text: #fff9;
  --pop-up-info-text: #ffffff80;
  --form-button-color: #202024;
}

